
.double-range {
  display: inline-block;
}
.double-range .barre{
   position: relative;
   margin-left:20px;
   margin-right:20px;
   padding: 12px 15px;
   clear:both;
   opacity:1;
    /* Styles */
   background: #fff;
   border-radius: 5px;
   border: 1px solid grey;
   box-shadow: 0 1px 0 rgba(0,0,0,0.2);
   outline: none;
}
.double-range.disabled{
  opacity:0.8;
}
.double-range .inside-range,
#colorScaleLegend{
    position:absolute;
    background: green;
    height:100%;
    top:0;
    left:0;
    max-width:100%;
}
.barre .range-slider{
    position:absolute;
    width:10px;
    top:-3px;
    bottom:-3px;
    background: #b5bdc8; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #b5bdc8 0%,#828c95 71%,#28343b 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    border: 1px solid rgba(0,0,0,0.6);
    box-shadow: 0 1px 0 rgba(0,0,0,0.2);
    pointer-events:auto;
    cursor:pointer;
}
.double-range.disabled .range-slider {
  cursor:none;
  pointer-events:none;
}
[id="begin-slider"]{
    left:-10px;
}
[id="end-slider"]{
    right:-10px;
}
.barre .step-value{
    position: absolute;
    font-size:0.8rem;
    font-weight:900;
    width: 30px;
    text-align: center;
    top:-22px;
    height:20px;
    background: rgba(240, 240, 240, 0.5)
}
.range-value{
    font-size:0.8em;
    width: 40px;
    text-align:center;
}
.range-value.range-min{
    float:left;
}
.range-value.range-max{
    float:right;
}
.double-range .linked{
  font-size:0.8em;
  text-align:center;
  margin-top: 5px;
}



